import { Injectable } from '@angular/core';
import { Observable, from } from 'rxjs';

//models
import { UserResultResponse } from '../models/response/users-result.model';

//services
import { HttpService } from './http.service';

@Injectable({ providedIn: 'root'})

export class DevicesService {
    constructor(
        private http: HttpService
    ){}

    getUsersList(): Observable<UserResultResponse[]> {
        return this.http.get<UserResultResponse[]>('devices/GetUsersLIst');
    }
}